.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background:#282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
  font-size: 12px;
}

h1 {
  font-size: 72px;
  background: linear-gradient(
    270deg, #3f5efb, #fc466b
  );
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.password {
  font-size: 72px;
  background: linear-gradient(
    270deg, #70d6ff, #ff70a6, #ff9770, #ffd670, #e9ff70
  );
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.login {
  font-size: 72px;
  background: linear-gradient(
    90deg, #3f5efb, #ffbe0b, #fc466b
  );
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

p  {
  animation: background-pan 10s linear infinite;
  background: linear-gradient(
      to right, #ffbe0b, #fb5607, #ff006e, #8338ec,
      #3a86ff, #00f5d4
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  white-space: nowrap;
}

button {
  position: relative;
  border: none;
  color: #ffffff;
  width: 25em;
  height: 3em;
  line-height: 2em;
  text-align: center;
  background: linear-gradient(
    90deg, #3f5efb, #fc466b, #03a9f4, #f441a5,
    #ffeb3b, #03a9f4, #3f5efb, #fc466b
  );
  background-size: 300%;
  border-radius: 30px;
  text-transform: uppercase;
  cursor: pointer;
  z-index: 1;
}
button:hover {
  animation: animation 8s linear infinite;
  border: none;
}
@keyframes animation {
  0% { background-position: 0%; }
  100% { background-position: 100%; }
}
button::before {
  content: "";
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  z-index: -1;
  background: linear-gradient(
    90deg, #3f5efb, #fc466b, #03a9f4, #f441a5,
    #ffeb3b, #03a9f4, #3f5efb, #fc466b
  );
  background-size: 400%;
  border-radius: 35px;
  transition: 1s;
}
button:hover::before {
  filter: blur(20px);
}

