::-webkit-scrollbar {
  height: 4px;              /* height of horizontal scrollbar ‚Üê You're missing this */
  width: 4px;               /* width of vertical scrollbar */
  border: 1px solid #d5d5d5;
}
* {
  box-sizing: border-box;
}
.container {
  display: flex;
  overflow-x: scroll;
  padding: 24px;
  width: 300px;
  scroll-snap-type: x mandatory;
  scroll-padding: 24px;
  border-radius: 8px;
  gap: 12px;
}
.container .item {
  background-color: #282c34;
  flex: 0 0 100%;
  padding: 24px;
  border-radius: 8px;
  scroll-snap-align: start;
}
.input-group {
  position: relative;
}
input {
  border: solid 1.5px #9e9e9e;
  border-radius: 1rem;
  background: none;
  padding: 1rem;
  font-size: 1rem;
  color: #f5f5f5;
  transition: 150ms cubic-bezier(0.4, 0, 0.2, 1);
}
label {
  position: absolute;
  left: 16px;
  color: #e8e8e8;
  pointer-events: none;
  transform: translateY(1rem);
  transition: 150ms cubic-bezier(0.4, 0, 0.2, 1);
}
input:focus {
  outline: none;
  border: 1.5px solid #1ab8e8;
}
input:focus ~ label {
  transform: translateY(-50%) scale(0.8);
  background-color: #020e26;
  padding: 0 0.2em;
  color: #2196f3;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
